<template>
  <div style="padding:10px">
    <Card>
        <div class="code-row-bg" :style="{ height:tabHeight + 'px' }">
            <div class="containAll" style="display:flex;justify-content:center;align-items:center;background:#163C69"  @click="createChannel">
              <div style="" @mouseenter="addmoveInFlag=true" @mouseleave="addmoveInFlag=false">
                  <div style="width:100%;">
                    <img :src="plus" alt="" style="vertical-align:middle;width:14px;marginRight:10px">
                    <span :style="{color:'rgba(21,141,222,1)','font-family':'Microsoft YaHei','font-size':'14px',cursor:isGray?'not-allowed':'pointer'}">新建通道</span>
                    <!-- <Icon type="md-add"  size='130'  :color="addmoveInFlag&&!isGray?'#0076ff':'gray'" :style="{cursor:isGray?'not-allowed':'pointer'}" @click.native="createChannel"/> -->
                  </div>
               </div>
            </div>
            <div  class="containAll" v-for="(item,index) in channelTableData" :key="index" @click="cardClick" @mouseenter="cardMouseenter(index)">
               <div class="containFirst">
                 <p style="display:flex;align-items:center">
                   <img :src="channelIco" alt="" style="width:40px;border:1px solid rgba(87,111,140,1);marginRight:8px">
                   <Tooltip >
                    <p style="width:100px;overflow: hidden;white-space: nowrap;text-overflow: ellipsis;font-size:16px;color:rgba(255,255,255,1)">{{item.channelName}}</p>
                    <div slot="content">
                        <p style="white-space: normal">
                          {{item.channelName}}
                        </p>
                    </div>
                  </Tooltip>
                 </p>
                 <Tooltip :content="item.isStartRecord===0?'关闭录制':'开启录制'" placement="top-start">
                    <i-switch size="small" true-color="#02e8ff" false-color="#3f7aa6" @on-change="channelChange" v-model="item.flag" :disabled="isGray" @click.native="channelChange1" ></i-switch>
                  </Tooltip>
               </div>

               <div class="containTime">
                  <p>
                    <label>创建时间:</label>
                    <span>{{item.createAt}}</span>
                  </p>
               </div>

               <div class="channlCardbottom">
                  <div :style="{cursor:'pointer'}"  @click="editChannel(item.channelId)">
                    <img :src="channelEditIco" alt="" >
                  </div>
                  <div :style="{cursor:isGray?'not-allowed':'pointer'}" @click="deleteChannel(item.channelId)">
                    <img :src="channelDeleteIco" alt="" >
                  </div>
               </div>
            </div>
          </div>
           <Page :total="totalCount" :current="pageNo" show-total show-sizer show-elevator style="text-align:right;margin:20px 0" @on-change="pageChange" @on-page-size-change="sizeChange"/>
    </Card>
     <!--新建通道-->
     <Modal
        id="channelNew_tab"
        v-model="newCreateChannelModal"
        width="640"
        :mask-closable="false"
       >
        <p slot="header" style="color:#fff;text-align:left">
           新建通道
        </p>
        <Form ref="channelNew" :model="channelData" :label-width="150" :rules="channelData.enterProtocol==='0'||channelData.enterProtocol==='1'?channelRules:channelData.enterProtocol==='2'?channelRules1:channelRules2" :key="refresh">
          <FormItem prop="channelName" label="通道名称：">
              <Input type="text" v-model="channelData.channelName" placeholder="请输入通道名称" :maxlength="60" style="width:350px"></Input>
               <span style="color:#AFBCCC;margin-left:19px">{{channelData.channelName.length}}/60</span>
          </FormItem>
           <FormItem prop="channelType" label="通道类型：" style="margin-top:-11px">
              <RadioGroup v-model="channelData.channelType">
                  <Radio v-for="(item,index) of channelTypeList" :label="item.metaDataValue.toString()" :key="index">
                    <span style="color:rgba(255,255,255,1);fontSize:14px">{{item.metaDataName}}</span> 
                </Radio>
              </RadioGroup>
          </FormItem>
          <FormItem prop="channelClass" label="通道分类：" style="margin-top:-15px">
            <div style="display:flex">
              <RadioGroup v-model="channelData.channelClass" style="max-height:80px;max-width:320px;overflow-y:auto">
                  <Radio v-for="(item) in channelClassAll" :label="item.categoryId" :key="item.categoryId"><span style="color:rgba(255,255,255,1);fontSize:14px">{{item.categoryName}}</span></Radio>
              </RadioGroup>
              <Button @click="addClass" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);height:26px;margin-left:10px">添加分类</Button>
            </div>
          </FormItem>
           <FormItem prop="enterProtocol" label="输入源协议：" style="margin-top:-10px">
              <Select v-model="channelData.enterProtocol" @on-change='change1' style="width:350px">
                <Option v-for="item in enterProtocolList" :value="String(item.metaDataValue)" :key="item.metaDataValue">{{ item.metaDataName }}</Option>
            </Select>
          </FormItem>
          <div v-if="channelData.enterProtocol==='0'||channelData.enterProtocol==='1'">
             <FormItem prop="address" label="源地址(主)：" style="margin-top:-9px">
                  <Input type="text" v-model="channelData.address" :maxlength="100" placeholder="请输入" style="width:350px"></Input>
                  <span style="color:#AFBCCC;margin-left:19px">{{channelData.address.length}}/100</span>
              </FormItem>
               <FormItem prop="address1" label="源地址(备)：" style="margin-top:-4px">
                  <Input type="text" v-model="channelData.address1" :maxlength="100" placeholder="请输入地址" style="width:350px" :disabled="isGray"></Input>
                  <span style="color:#AFBCCC;margin-left:19px">{{channelData.address1.length}}/100</span>
              </FormItem>
                <FormItem prop="timeShifting" label="时移时长(分钟)：" style="margin-top:-2px">
                  <Input type="textarea" v-model="channelData.timeShifting" :placeholder="`直播过程中可以回看时移的最长时间,不填写系统默认4个小时,即240分钟,可输入的最大值${maxShiftAndBackTimeLenth}`" style="width:350px"></Input></br>
              </FormItem>
              <FormItem prop="storageTime" label="可存储的时间(分钟)：" style="margin-top:-1px">
                  <Input type="textarea" v-model="channelData.storageTime" :placeholder="`录制的视频,后台最长可存储的时间,不填写系统默认7天,即10080分钟,可输入的最大值${maxShiftAndBackTimeLenth}`"  style="width:350px"></Input></br>
              </FormItem>
              <FormItem prop="playProtocol" label="播放协议：" style="margin-top:-2px">
                  <Select v-model="channelData.playProtocol" style="width:350px">
                   <Option v-for="(item,index) in playProtocolList" :value="item.metaDataValue.toString()" :key="index">{{ item.metaDataName}}</Option>
                </Select> 
              </FormItem>
          </div>
           <div v-if="channelData.enterProtocol==='2'">
             <FormItem prop="userName" label="用户名：">
              <Input type="text" v-model="channelData.userName" :maxlength="100" placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
             <FormItem prop="passWord" label="密码：">
              <Input type="text" v-model="channelData.passWord" :maxlength="100" placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
          </div>
          <div v-if="channelData.enterProtocol==='3'">
             <FormItem prop="localSip" label="本地SIP端口：">
              <Input type="text" v-model="channelData.localSip" placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
             <FormItem prop="sipServerId" label="SIP服务器ID：">
              <Input type="text" v-model="channelData.sipServerId" placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
              <FormItem prop="sipServerArea" label="SIP服务器域：">
              <Input type="text" v-model="channelData.sipServerArea"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
              <FormItem prop="sipServerAddress" label="SIP服务器地址：">
              <Input type="text" v-model="channelData.sipServerAddress"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
              <FormItem prop="sipServerPort" label="SIP服务器端口：">
              <Input type="text" v-model="channelData.sipServerPort"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
              <FormItem prop="sipUserConfirmId" label="SIP用户认证ID：">
              <Input type="text" v-model="channelData.sipUserConfirmId"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
              <FormItem prop="sipPassWord" label="密码：">
              <Input type="text" v-model="channelData.sipPassWord"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
              <FormItem prop="registerActiveTime" label="注册有效期(秒)：">
              <Input type="text" v-model="channelData.registerActiveTime"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
            <FormItem prop="heartbeatPeriod" label="心跳周期(秒)：">
              <Input type="text" v-model="channelData.heartbeatPeriod"  placeholder="请输入用户名"  style="width:350px"></Input>
            </FormItem>
          </div>
          <div  v-if="channelData.enterProtocol==='4'">
             <FormItem prop="gkIsStart" label="启用GK：">
                <Select v-model="channelData.gkIsStart" style="width:200px">
                  <Option value="0">启用</Option>
                  <Option value="1">禁用</Option>
              </Select> 
            </FormItem>
             <FormItem prop="gkAddress" label="GK地址：">
              <Input type="text" v-model="channelData.gkAddress" style="width:350px"></Input>
            </FormItem>
             <FormItem prop="E164" label="E.164：">
              <Input type="text" v-model="channelData.E164"  placeholder="请输入用户名" style="width:350px"></Input>
            </FormItem>
             <FormItem prop="H3231D" label="H.3231D：">
              <Input type="text" v-model="channelData.H3231D"  placeholder="请输入用户名" style="width:350px"></Input>
            </FormItem>
             <FormItem prop="gkUserName" label="用户名：">
              <Input type="text" v-model="channelData.gkUserName"  placeholder="请输入用户名" style="width:350px"></Input>
            </FormItem>
             <FormItem prop="gkPassword" label="密码：">
              <Input type="text" v-model="channelData.gkPassword"  placeholder="请输入用户名" style="width:350px"></Input>
            </FormItem>
          </div>
          <div  v-if="channelData.enterProtocol==='5'">
             <FormItem prop="internetPort" label="网络出口：">
                <RadioGroup v-model="channelData.internetPort">
                  <Radio label="LAN">
                      <span>LAN</span>
                  </Radio>
                  <Radio label="WAN">
                    <span>WAN</span>
                  </Radio>
              </RadioGroup>
            </FormItem>
             <FormItem prop="transferType" label="传输类型：">
                <Select v-model="channelData.transferType" style="width:200px">
                  <Option value="UDP">UDP</Option>
                </Select> 
            </FormItem>
             <FormItem prop="registerServer" label="注册服务器：">
                <Select v-model="channelData.registerServer" style="width:200px">
                  <Option value="0">启用</Option>
                  <Option value="1">禁用</Option>
                </Select> 
            </FormItem>
             <FormItem prop="domainAddress" label="域名地址：">
              <Input type="text" v-model="channelData.domainAddress"  placeholder="请输入域名地址" style="width:300px"></Input>
            </FormItem>
             <FormItem prop="serverAddress" label="服务器地址：">
              <Input type="text" v-model="channelData.serverAddress"  placeholder="请输入服务器地址" style="width:300px"></Input>
            </FormItem>
              <FormItem prop="interUserName" label="用户名：">
              <Input type="text" v-model="channelData.interUserName"  placeholder="请输入用户名" style="width:300px"></Input>
            </FormItem>
             <FormItem prop="interPassword" label="密码：">
              <Input type="text" v-model="channelData.interPassword"  placeholder="请输入密码" style="width:300px"></Input>
            </FormItem>
          </div>
       </Form>
       <p slot="footer">
          <div style="text-align:center;margin-top:-5px">
            <Button style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);margin-right:50px" @click="cancelCreateChannel">取消</Button>
            <Button type="primary" :loading="channelNewLoading" @click="confirmCreateChannel('channelNew')">确定</Button>
           </div>
       </p>
    </Modal>
     <!--修改通道-->
     <Modal
        v-model="editChannelModal"
        :mask-closable="false"
        width="600"
        id="channelEdit_tab"
       >
       <p slot="header">
          修改通道
       </p>
        <Form ref="channelEdit" :model="channelData" :label-width="150" :rules="channelRules">
          <FormItem prop="channelName" label="通道名称：">
              <Input type="text" v-model="channelData.channelName" placeholder="请输入通道名称" style="width:350px" :disabled="isGray" :maxlength="60"></Input>
               <span style="color:#fff">{{channelData.channelName.length}}/60</span>
          </FormItem>
            <FormItem prop="address" label="源地址(主)：">
              <Input type="text" v-model="channelData.address" :maxlength="100" placeholder="请输入地址" style="width:350px" :disabled="isGray"></Input>
              <span style="color:#fff">{{channelData.address.length}}/100</span>
          </FormItem>
          <FormItem prop="address1" label="源地址(备)：">
              <Input type="text" v-model="channelData.address1" :maxlength="100" placeholder="请输入地址" style="width:350px" :disabled="isGray"></Input>
              <span style="color:#fff">{{channelData.address1.length}}/100</span>
          </FormItem>
            <FormItem prop="timeShifting" label="时移时长(分钟)：">
              <Input type="textarea" v-model="channelData.timeShifting" :placeholder="`直播过程中可以回看时移的最长时间,不填写系统默认4个小时,即240分钟,可输入的最大值${maxShiftAndBackTimeLenth}`" style="width:350px" :disabled="isGray"></Input></br>
          </FormItem>
           <FormItem prop="storageTime" label="可存储的时间(分钟)：">
              <Input type="textarea" v-model="channelData.storageTime" :placeholder="`录制的视频,后台最长可存储的时间,不填写系统默认7天,即10080分钟,可输入的最大值${maxShiftAndBackTimeLenth}`" style="width:350px" :disabled="isGray"></Input></br>
          </FormItem>
            <FormItem prop="playProtocol" label="播放协议：">
               <Select v-model="channelData.playProtocol" style="width:350px" :disabled="isGray">
                <Option v-for="(item,index) in playProtocolList" :value="item.metaDataValue.toString()" :key="index">{{ item.metaDataName }}</Option>
             </Select> 
          </FormItem>
       </Form>
       <p slot="footer">
         <div style="text-align:center">
           <Button style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);margin-right:50px" @click="cancelEditChannel">取消</Button>
           <Button type="primary" @click="confirmEditChannel('channelEdit')" :loading="channelEditLoading" :disabled="isGray">确定</Button>
          </div>
       </p>
    </Modal>
      <!--录制计划确认框-->
      <Modal
          v-model="recodePlanConfirmModal"
          width="640"
          id="recodePlanConfirmModal_2"
          class-name="recodePlanClassName2"
          :closable="false"
          :mask-closable="false"
        >
        <Form ref="formInline" style="margin-top:52px;padding:0 68px">
          <FormItem>
            <span style="fontSize:16px;font-weight:400;font-family:Microsoft YaHei;color:rgba(254,254,254,1)">是否继续遵循录制计划执行?</span>
            <label style="margin-left:45px">
              <RadioGroup v-model="isStart" style="color:#fff">
                  <Radio :label="1">是</Radio>
                  <Radio :label="0">否</Radio>
              </RadioGroup>
            </label> 
          </FormItem>
          <FormItem style="marginTop:42px">
            <div style="color:rgba(175,188,204,1);font-weight:400;font-size:14px;font-family:Microsoft YaHei;">
              当您选择"是",在执行打开/关闭录制操作后,系统后台依然会按照各个通道的录制计划中的时间安排,自动执行打开或关闭录制的指令。
            </div>
          </FormItem>
          <FormItem>
            <div style="color:rgba(175,188,204,1);font-weight:400;font-size:14px;font-family:Microsoft YaHei;">
              当您选择"否",录制计划中的时间表将失效，后续需要您再手动开启或关闭录制开关,或重新激活录制计划。
            </div>
          </FormItem>
        </Form>
        <p slot="footer" style="display:flex;justify-content:center;margin-top:38px;margin-bottom:20px">
            <Button @click="startOrStopCancel" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">取消</Button>
            <Button type="primary" @click="startOrStopConfirm" :disabled="isGray" style="margin-left:49px">确定</Button>
        </p>
      </Modal>

      <!--添加分类-->
     <Modal
        v-model="addClassModal"
        title="添加分类"
      >
      <Input v-model="addClassInput" placeholder="请输入分类名称" style="width: 300px" :maxlength="20"/>
      <span style="color:#fff">{{addClassInput.length}}/20</span>
      <Button type="primary" @click="confirmAdd" style="margin-left:8px">确定</Button>
      <Button  @click="confirmCancel" style="margin-left:8px;background:rgba(13,67,111,1);color:rgba(199,219,244,1)">关闭</Button>
      <p slot="footer"></p>
    </Modal>
  </div>
</template>
<script>
import api from '@/api/api'
import plus from '@/images/plus.png'
import channelIco from '@/images/channel_ico.png'
import channelEditIco from '@/images/channel_edit.png'
import channelDeleteIco from '@/images/channel_delete.png'
export default {
  data (){
    const validateTimeShifting=(rule, value, callback) => {
      let reg = /^[1-9]\d*$/
      if (!value) {
        callback()
      } else {
        if(reg.test(value)){
          callback()
        }else{
          callback(new Error('请输入有效时移时长(正整数)'))
        }
      }
    }
    const validateStorageTime=(rule, value, callback) => {
      let reg = /^[1-9]\d*$/
        if (!value) {
          callback()
        } else {
          if(reg.test(value)){
            callback()
          } else {
            callback(new Error('请输入有效可存储时长(正整数)'))
          }
      }
    }
    const validateChannelName=(rule, value, callback) => {
      let reg = /^[a-zA-Z0-9_\u4e00-\u9fa5]+$/
      if(!reg.test(value)){
        callback(new Error("请输入中文、数字、字母、下划线"))
      }else{
        callback()
      }
    }
    /**时移校验 */
    const validatemaxShift = (rule, value, callback) => {
      let maxShiftAndBackTimeLenth = this.maxShiftAndBackTimeLenth
      if(!value){
        callback()
      }else{
        if(maxShiftAndBackTimeLenth<value){
          callback(new Error(`可输入最大值是${maxShiftAndBackTimeLenth}`))
        }else{
          callback()
        }
      }
    }
     /**存储校验 */
    const validatemaxStorage = (rule, value, callback) => {
      let maxShiftAndBackTimeLenth = this.maxShiftAndBackTimeLenth
      if(!value){
        callback()
      }else{
        if(maxShiftAndBackTimeLenth<value){
          callback(new Error(`可输入最大值是${maxShiftAndBackTimeLenth}`))
        }else{
          callback()
        }
      }
    }
    return {
      plus,
      channelIco,
      channelEditIco,
      channelDeleteIco,
      channelNewLoading:false,
      channelEditLoading:false,//修改通道确定按钮loading状态
      recodePlanConfirmModal:false,
      channelClassAll:[],
      channelTypeList:[],
      enterProtocolList:[
        {
          value:'TS/UDP',
          label:'TS/UDP',
        },
        {
          value:'RTP',
          label:'RTP',
        },
        {
          value:'ONVIF',
          label:'ONVIF',
        },
        {
          value:'GB28181',
          label:'GB28181',
        }
    ],
    playProtocolList:[],
      refresh:0,
      newCreateChannelModal:false,//新建通道弹窗
      addmoveInFlag:false,
      editChannelModal:false,//编辑通道弹窗
      addClassModal:false,//添加分类弹窗
      addClassInput:'',
      channelTableData:[],
     channelData:{
        channelName:'',//通道名称
        channelType:'0',
        enterProtocol:'0',//输入源协议
        address:'',
        address1:'',
        timeShifting:'',//时移时长
        storageTime:'',//可存储时长
        playProtocol:'',
        channelClass:'',
        userName:'',
        passWord:'',
        localSip:'',
        sipServerId:'',
        sipServerArea:'',
        sipServerAddress:'',
        sipServerPort:'',
        sipUserConfirmId:'',
        sipPassWord:'',
        heartbeatPeriod:'',
        gkIsStart:'0',
        gkAddress:'',
        E164:'',
        H3231D:'',
        gkUserName:'',
        gkPassword:'',
        internetPort:'LAN',
        transferType:'UDP',
        registerServer:'0',
        domainAddress:'',
        serverAddress:'',
        interUserName:'',
        interPassword:'', 
        activeIndex:0,
      },
      channelId:'',
       channelRules:{
            channelName: [
              { required:true,message: '请输入通道名称', trigger: 'blur' },
              {validator: validateChannelName, trigger: 'blur'}
            ],
            channelType: [
              { required: true, message: '请选择通道类型', trigger: 'blur' }
            ],
            enterProtocol:[
              { required: true, message: '请选择源协议', trigger: 'change' }
            ],
            address:[
              { required: true, message: '请输入源地址(主)', trigger: 'blur' }
            ],
            address1:[
              { required: true, message: '请输入源地址(备)', trigger: 'blur' }
            ],
            playProtocol:[
              { required: true, message: '请选择播放协议', trigger: 'change' }
            ],
              timeShifting:[
              {validator: validateTimeShifting, trigger: 'blur' },
              {validator: validatemaxShift, trigger: 'blur' }
            ],
            storageTime:[
              {validator: validateStorageTime, trigger: 'blur' },
              {validator:validatemaxStorage,trigger: 'blur'}
            ]
          },
           channelRules1:{
              channelName: [
                { required:true,message: '请输入通道名称', trigger: 'blur' }
              ],
              channelType: [
                { required: true, message: '请选择通道类型', trigger: 'blur' }
              ],
              enterProtocol:[
                { required: true, message: '请选择源协议', trigger: 'change' }
              ],
              address:[
                { required: true, message: '请输入源地址', trigger: 'change' }
              ],
              playProtocol:[
                { required: true, message: '请选择播放协议', trigger: 'change' }
              ],
              userName:[
                { required: true, message: '请输入用户名称', trigger: 'change' }
              ],
               passWord:[
                { required: true, message: '请输入密码', trigger: 'change' }
              ]
          },
           channelRules2:{
              channelName: [
                { required:true,message: '请输入通道名称', trigger: 'blur' }
              ],
              channelType: [
                { required: true, message: '请选择通道类型', trigger: 'blur' }
              ],
              enterProtocol:[
                { required: true, message: '请选择源协议', trigger: 'change' }
              ],
          },
          //===========录制计划
           isStart:1,
           isStartFlag:true,//是否开启标志
           renderContent:(h) => {
            return h('div',[
              h('p',[
                h('Icon',{
                  props:{
                    type:'md-help-circle',
                    size:20,
                    color:'yellow'
                  }
                }),
                h('span',{
                  style:{
                    fontWeight:700
                  }
                },'是否还要遵循录制计划的执行吗?'),
                  h('RadioGroup',{
                      props:{
                        value:this.isStart
                      },
                      style:{
                        marginLeft:'5px'
                      },
                      on:{
                        'on-change':val =>{
                          this.isStart = val
                        }
                      }
                    },[
                      h('Radio',{
                        props:{
                          size:'small',
                          label:1
                        }
                      },'是'),
                      h('Radio',{
                        props:{
                          size:'small',
                          label:0
                        }
                      },'否')
                  ]),
              ]),
              h('div',[
                    h('p',{
                      style:{
                        paddingLeft:'20px'
                      }
                    },'当您选择"是",在执行打开/关闭录制操作后,系统后台依然会按照各个通道的录制计划中的时间安排,自动执行打开或关闭录制的指令.'),
                    h('p',{
                      style:{
                        paddingLeft:'20px'
                      }
                    },'当您选择"否",录制计划中的时间表将失效,后续需要您再手动开启或关闭录制开关,或重新激活录制计划.')
                ])
              ])
          },
          //===========分页
          pageNo:1,
          pageSize:10,
          totalCount:0,
          isGray:false,
          maxShiftAndBackTimeLenth:0,

          base:1,   // 分辨率自适应 计算后的倍数
          tabHeight:0,  // 分辨率自适应调整：表格的高度
    }
  },
  created(){
    this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true; //是否需要置灰（无权限操作需要）
    //查看分类
    this.searchRecordCategoryList()
    let selectAllData = JSON.parse(localStorage.getItem('selectAllData'))
    //输入协议
    this.enterProtocolList = selectAllData[2].metaDictList
    //通道类型
    this.channelTypeList = selectAllData[6].metaDictList
    //播放协议
    this.playProtocolList = selectAllData[3].metaDictList
    this.channelData.playProtocol = this.playProtocolList[0].metaDataValue.toString()
    console.log('哈哈哈',selectAllData)
    this.getPublicConfig()
  },
  mounted(){
    this.autoFix();
  },
  methods:{

    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      // this.tabHeight = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
      this.cardToParent();
    },

    /** 子组件向父组件传值：将子组件的base传给父组件 */
    cardToParent(){
      this.$emit('cardEvent', this.base);
    },

    /**获取公共配置 */
    getPublicConfig(){
      api.getPublicConfig({}).then(res=>{
        this.maxShiftAndBackTimeLenth = res.result.maxShiftAndBackTimeLenth
      })
    },
    change1(){
      this.refresh++
    },
    addClass(){
        this.addClassModal = true
        this.addClassInput = ''
      },
      confirmAdd(){
        if(!this.addClassInput){
           this.$Message.info('请输入分类名称')
           return false
        }else{
          let reg = /^[a-zA-Z0-9_\u4e00-\u9fa5]+$/
          if(!reg.test(this.addClassInput)) {
            this.$Message.info("请输入中文、数字、字母、下划线")
            return false
          }
          // else{
          //   for(let item of this.channelClassAll){
          //    if(item.name === this.addClassInput) {
          //      this.$Message.info("分类名称已存在")
          //      return false
          //    }
          //   }
          // }

          let param = {
            name:this.addClassInput
          }
          api.createCategory(param).then(res=>{
            this.searchRecordCategoryList()
            this.addClassModal = false
          })
        }
      },
      /**确定分类取消 */
      confirmCancel(){
        this.addClassModal = false
      },
        //查询全部分类
      searchRecordCategoryList(){
        let param = {
          name:'',
          pageNo:-1,
        }
        api.searchRecordCategoryList(param).then(res=>{
          console.log('分类',res)
          this.channelClassAll = res.result
        })
      },
    //------------------------通道相关接口
      showChannelList(name){
        this.activeName = name
        this.pageNo = 1
        this.pageSize = 10
        this.searchRecordChannelList(name)
      },
      //查询通道列表
      searchRecordChannelList(name){
         let param = {
           channelName:name,
           pageNo:this.pageNo,
           pageSize:this.pageSize
        }
        api.searchRecordChannelList(param).then(res=>{
          console.log('通道列表',res)
          this.channelTableData = res.result
          this.totalCount = res.count
          if(res.result&&res.result.length){
            for(let item of res.result){
              item.channelType1 = item.channelType === 0?'视频+音频': item.channelType === 1?'纯视频':'纯音频'
              item.sourceType1 = item.sourceType === '0'?'TS/UDP':item.sourceType === '1'?'RTP':item.sourceType === '2'?'ONVIF':item.sourceType === '3'?'GB28181':item.sourceType === '4'?'H.323':'SIP'
              item.flag = item.isStartRecord === 0?true:false
            }
             /**更改缓存 */
            let arr = sessionStorage.getItem('monitorState')?JSON.parse(sessionStorage.getItem('monitorState')):[]
            if(arr){
              for(let val of arr){
                for(let val1 of res.result){
                  if(val.channelId === val1.channelId){
                    val.business.isRecording = val1.isStartRecord === 0?true:false
                  }
                }
              }
              sessionStorage.setItem('monitorState',JSON.stringify(arr))
            }
          }else if(res.result&&!res.result.length&&this.pageNo>1){
            this.pageNo--
            this.searchRecordChannelList()
          }
        })
      },
    //创建通道
    createChannel(){
      if (this.isGray == true) {
        return false;
      }
      this.$refs['channelNew'].resetFields()
       this.newCreateChannelModal = true
        this.channelData.enterProtocol = '0'
        this.channelData.channelClass = ''
        //初始化分类
        console.log(this.channelClassAll)
        this.newCreateChannelModal = true  
    },
    //创建通道取消
    cancelCreateChannel(){
       this.newCreateChannelModal = false
    },
    //创建通道确认
    confirmCreateChannel(name){
       this.$refs[name].validate((valid) => {
            if (valid) {
              let param = {}

              // let flag1 = this.channelData.address.startsWith("UDP://");
              // let flag2 = this.channelData.address.startsWith("udp://");
              // let flag3 = this.channelData.address1.startsWith("UDP://");
              // let flag4 = this.channelData.address1.startsWith("udp://");

              // if ((!flag1 && !flag2) || (!flag3 && !flag4)) {
              //   this.$Message.info('源地址格式不正确，请输入以“UDP://”或“udp://”开头的源地址');
              //   return false;
              // }

              /** 修改 禅道2325bug */
              let flag1 = this.channelData.address.startsWith("udp://");
              let flag2 = this.channelData.address1.startsWith("udp://");
              if (!flag1 || !flag2) {
                this.$Message.info('源地址格式不正确，请输入以“udp://”开头的源地址');
                return false;
              }

              console.log('id',this.channelData.channelClass)
              if(this.channelData.enterProtocol === '0'){
                 param={
                  userId:JSON.parse(localStorage['loginRes']).user.userId,
                  channelName:this.channelData.channelName,
                  channelType:this.channelData.channelType,
                  categoryId:this.channelData.channelClass,
                  sourceType:this.channelData.enterProtocol,//输入性协议
                  tsudpSource:this.channelData.address,//源地址
                  tsudpBakSource:this.channelData.address1,
                  tsudpStoreTimelength:this.channelData.storageTime,//可存储时长
                  tsudpShiftTimeLength:this.channelData.timeShifting,//时移时长
                  tsudpPlayType:this.channelData.playProtocol,//udp播放协议
                }
              }else if(this.channelData.enterProtocol ==="1"){
                param={
                  userId:JSON.parse(localStorage['loginRes']).user.userId,
                  channelName:this.channelData.channelName,
                  channelType:this.channelData.channelType,
                  categoryId:this.channelData.channelClass,
                  sourceType:this.channelData.enterProtocol,//输入性协议
                  rtpSource:this.channelData.address,//源地址
                  rtpBakSource:this.channelData.address1,
                  rtpStoreTimelength:this.channelData.storageTime,//可存储时长
                  rtpShiftTimeLength:this.channelData.timeShifting,//时移时长
                  rtpPlayType:this.channelData.playProtocol,//rtp播放协议
                }  
              }
              this.channelNewLoading = true
              api.createChannel(param).then(res=>{
                this.$Message.success('通道创建成功')
                this.newCreateChannelModal = false
                this.channelNewLoading = false
                this.searchRecordChannelList()
              },err=>{
                this.channelNewLoading = false
              })
            } else {
              this.$Message.error('请输入符合规则的内容');
            }
        })
    },
    //编辑通道
     editChannel(id){
       event.stopPropagation()
       this.channelId = id
       this.$refs['channelEdit'].resetFields()
         this.editChannelModal = true
        //查询详情
        let param = {
          channelId:id
        }
        api.selectChannelDetail(param).then(res=>{
          console.log(res)
          this.channelData.channelName = res.result.channelName
          this.channelData.channelType = res.result.channelType
          this.channelData.enterProtocol = res.result.sourceType
          if(res.result.sourceType === '0'){
            this.channelData.playProtocol = res.result.tsudpPlayType
            this.channelData.timeShifting = res.result.tsudpShiftTimeLength
            this.channelData.storageTime = res.result.tsudpStoreTimelength
            this.channelData.address = res.result.tsudpSource
            this.channelData.address1 = res.result.tsudpBakSource?res.result.tsudpBakSource:''
          }else if(res.result.sourceType === '1'){
            this.channelData.address = res.result.rtpSource
            this.channelData.address1 = res.result.rtpBakSource?res.result.rtpBakSource:''
            this.channelData.playProtocol = res.result.rtpPlayType
            this.channelData.timeShifting = res.result.rtpShiftTimeLength
            this.channelData.storageTime = res.result.rtpStoreTimelength
          }
        })
     },
    //编辑通道确认
    confirmEditChannel(name){
      this.$refs[name].validate((valid) => {
        if (valid) {
          let param = {}

          // let flag1 = this.channelData.address.startsWith("UDP://");
          // let flag2 = this.channelData.address.startsWith("udp://");
          // let flag3 = this.channelData.address1.startsWith("UDP://");
          // let flag4 = this.channelData.address1.startsWith("udp://");

          // if ((!flag1 && !flag2) || (!flag3 && !flag4)) {
          //   this.$Message.info('源地址格式不正确，请输入以“UDP://”或“udp://”开头的源地址');
          //   return false;
          // }

          /** 修改 禅道2325bug */
          let flag1 = this.channelData.address.startsWith("udp://");
          let flag2 = this.channelData.address1.startsWith("udp://");
          if (!flag1 || !flag2) {
            this.$Message.info('源地址格式不正确，请输入以“udp://”开头的源地址');
            return false;
          }

            if(this.channelData.enterProtocol === '0'){
               param = {
                  userId:JSON.parse(localStorage['loginRes']).user.userId,
                  channelId:this.channelId,
                  channelName:this.channelData.channelName,
                  channelType:this.channelData.channelType,
                  tsudpSource:this.channelData.address,
                  tsudpBakSource:this.channelData.address1,
                  sourceType:this.channelData.enterProtocol,
                  tsudpStoreTimelength:this.channelData.storageTime,
                  tsudpShiftTimeLength:this.channelData.timeShifting,
                  tsudpPlayType:this.channelData.playProtocol
                }
            }else if(this.channelData.enterProtocol === '1'){
              param = {
                  userId:JSON.parse(localStorage['loginRes']).user.userId,
                  channelId:this.channelId,
                  channelName:this.channelData.channelName,
                  channelType:this.channelData.channelType,
                  rtpSource:this.channelData.address,
                  rtpBakSource:this.channelData.address1,
                  sourceType:this.channelData.enterProtocol,
                  rtpStoreTimelength:this.channelData.storageTime,
                  rtpShiftTimeLength:this.channelData.timeShifting,
                  rtpPlayType:this.channelData.playProtocol
                }
            }
            this.channelEditLoading = true
            api.modifyChannel(param).then(res=>{
              this.$Message.success('通道编辑成功');
              this.editChannelModal = false
              this.searchRecordChannelList()
              this.channelEditLoading = false
            },err=>{
              this.channelEditLoading = false
            })
        } else {
          this.$Message.error('请输入符合规则的内容');
        }
      })
    },
    //编辑通道取消
    cancelEditChannel(){
      this.editChannelModal = false
    },
    //删除通道
    deleteChannel(id){
      if(this.isGray){
        event.stopPropagation();
        return false
      }
      event.stopPropagation();
      this.$Modal.confirm({
        render: (h) => {
          return h('div',[
            h('p',{
              style:{
                color:'#fff'
              }
            },'您确定要删除该通道吗?')
          ])
        },
        onOk: () => {
          let param = {
            channelId:id
          }
          api.deleteChannel(param).then(res=>{
            this.$Message.success('通道删除成功')
            this.searchRecordChannelList(this.activeName)
          })
         },
         onCancel: () => {
          
         }
      })
    },
    cardMouseenter(index){
      //  alert(index)
      this.activeIndex = index
    },
    channelChange1(){
       event.stopPropagation()
    },
    //开始录制/结束录制
    channelChange(value){
      console.log("channelChange", value);
      this.recodePlanConfirmModal = true
      this.isStartFlag = value;
      // this.$Modal.confirm({
      //   render: this.renderContent,
      //   onOk: () => {
      //     if(value){
      //       //打开开始录制
      //       let param = {
      //         channelIdList:[this.channelTableData[this.activeIndex].channelId],
      //         isStartRecord:0,//开启录制
      //         autoRecordPlan:this.isStart
      //       }
      //       api.startOrStopRecord(param).then(res=>{
      //         this.$Message.success('录制已开启')
      //         this.searchRecordChannelList()
      //       })
      //     }else{
      //         let param = {
      //         channelIdList:[this.channelTableData[this.activeIndex].channelId],
      //         isStartRecord:1,//关闭录制
      //         autoRecordPlan:this.isStart
      //       }
      //       api.startOrStopRecord(param).then(res=>{
      //         this.$Message.success('录制已关闭')
      //         this.searchRecordChannelList()
      //       })
      //     }
      
      //   },
      //   onCancel: () => {
      //   }
      // });
    },
    //
    startOrStopConfirm(){
      //开启
      if(this.isStartFlag){
        let param = {
          channelIdList:[this.channelTableData[this.activeIndex].channelId],
          isStartRecord:0,//开启录制
          autoRecordPlan:this.isStart
        }
        api.startOrStopRecord(param).then(res=>{
          this.$Message.success('录制已开启')
          this.recodePlanConfirmModal = false
          this.searchRecordChannelList()
        })
      }else{
        let param = {
          channelIdList:[this.channelTableData[this.activeIndex].channelId],
          isStartRecord:1,//关闭录制
          autoRecordPlan:this.isStart
        }
        api.startOrStopRecord(param).then(res=>{
          this.$Message.success('录制已关闭')
          this.recodePlanConfirmModal = false
          this.searchRecordChannelList()
        })
      }
    },
    startOrStopCancel(){
      this.recodePlanConfirmModal = false
      this.searchRecordChannelList()
    },
    //通道卡片点击
    cardClick(){
      this.$router.push({
          name:'channelMonitor'
      })
    },
    //=========================分页部分
      pageChange(page){
        this.pageNo = page
        this.searchRecordChannelList()
      },
      sizeChange(size){
        this.pageSize = size
        this.searchRecordChannelList()
      }
  }
}
</script>
<style>
  .containAll{
    width:254px;
    height:159px;
    border:1px solid rgba(105,133,166,1);
    margin-left:10px;
