    background:#255b8d;
  }
  .containAll:hover {
    border:1px solid rgba(21,141,222,1)
  }
  .code-row-bg{
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    /* height: 450px; */
    overflow-y: auto
  }
  .containFirst {
    height: 40px;
    margin-top:20px;
    display: flex;
    justify-content: space-between;
    padding:0 10px;
  }
  .containTime{
    font-size:14px;
    font-family:'Microsoft YaHei';
    font-weight:400;
    color:rgba(199,219,244,1);
    padding:0 10px;
    margin-top: 20px
  }
  .channlCardbottom{
    border-top:1px solid rgba(105,133,166,1);
    margin-top:20px;
    height: 36px;
    display: flex;
    background: #2c689b;
  }
   .channlCardbottom div{
     width: 50%;
     display: flex;
     justify-content: center;
     align-items: center
   }
    .channlCardbottom div:first-child{
      border-right: 1px solid rgba(105,133,166,1);
   }
    .channlCardbottom div:hover{
      background: #2d8cf0
   }
   .channlCardbottom img{
      width: 18px
   }
   #channelNew .ivu-form-item-content,#channelEdit .ivu-form-item-content{
    line-height: 22px
  }
  #channelNew .ivu-modal-header,#channelEdit .ivu-modal-header{
    background: #3aa7dd
  }
  #recodePlanConfirmModal_2 .ivu-modal-content{
    background: #0D436F
  }
  #recodePlanConfirmModal_2 .ivu-modal-footer{
    border-top: hidden
  }
  input::-webkit-input-placeholder,
  textarea::-webkit-input-placeholder {
      color: #AFBCCC!important;
      font-size: 14px;  
  }
  .recodePlanClassName2{
    left: -17px;
    top:15px
  }
</style>





